@import "../node_modules/bootstrap/scss/functions";

// variables
// primarysecondary
$primary: #fb5830;
$secondary: #d0021c;
$gray-700: #596878;

@import "../node_modules/bootstrap/scss/variables";

// fs-7fs-8
$font-sizes: map-merge(
  $font-sizes,
  (
    7: 0.9rem,
    8: 0.8rem,
  )
);

// bg-linebg-gray-700bg-gray-200
$theme-colors: map-merge(
  $theme-colors,
  (
    "line": #f97160,
    "gray-700": $gray-700,
    "gray-200": $gray-200,
    // variables: $gray-200
  )
);

@import "../node_modules/bootstrap/scss/maps";
@import "../node_modules/bootstrap/scss/mixins";
@import "../node_modules/bootstrap/scss/utilities";

// utilitieswidth()
$utilities: map-merge(
  $utilities,
  (
    "width":
      map-merge(
        map-get($utilities, "width"),
        (
          responsive: true,
          values:
            map-merge(
              map-get(map-get($utilities, "width"), "values"),
              (
                //  w-50pxw-2px
                "50px": 50px,
                "2px": 2px
              )
            ),
        )
      ),
    "height":
      map-merge(
        map-get($utilities, "height"),
        (
          responsive: true,
          values:
            map-merge(
              map-get(map-get($utilities, "height"), "values"),
              (
                //  h-80px
                "80px": 80px
              )
            ),
        )
      ),
  )
);

// utilitiescursetransformtransition()
$utilities: map-merge(
  $utilities,
  (
    "cursor": (
      // responsive: true,
      property: cursor,
      class: cursor,
      values: pointer,
      // classcursor-pointer
    ),
    "transform": (
      // responsive: true,
      property: transform,
      values: (
        rotate-180: rotate(180deg),
        // js classList.add("transform-rotate-180")
      ),
    ),
    "transition": (
      // responsive: true,
      property: transition,
      values: (
        transform: transform 0.2s,
        // classtransition-transform
      ),
    ),
  )
);

@import "../node_modules/bootstrap/scss/bootstrap";
